::-moz-selection,
::selection {
  background: #660649;
  color: #fff;
  text-shadow: none;
}

aside {
  margin-top: 125px;
}

aside .widget ul {
  padding-left: 1rem;
}

aside .widget h2 {
  font-size: 1.75rem;
  margin-bottom: .5rem;
}

section .currently-reading p {
  margin: 0;
}

.widget_links p {
  margin-bottom: 0;
}

#audiofiction .blogroll,
#writers .blogroll {
  column-count: 2;
}

@media screen and (max-width: 768px) {
  #audiofiction .blogroll,
  #writers .blogroll {
    column-count: 1;
  }
}

.widget_rss h2 {
  overflow: hidden;
}

.widget_rss h2 a {
  float: left;
}

.rsswidget img {
  margin: 8px 8px 0 0;
}

li.linkcat {
  list-style: none;
}

a:link {
  color: #4e6795;
}

